.card{background:#fff;border-radius:16px;box-shadow:0 1px 3px rgba(0,0,0,.12);overflow:hidden;transition:all .3s ease;position:relative;height:100%;display:flex;flex-direction:column}.card:hover{transform:translateY(-5px);box-shadow:0 4px 6px rgba(0,0,0,.1)}.card.predefined{border:2px solid rgba(67,97,238,.2)}.card.predefined:hover{border-color:rgba(67,97,238,.4)}.card.pulse{animation:pulse 1s}.cardInner{display:flex;flex-direction:column;height:100%}.header{display:flex;justify-content:space-between;align-items:flex-start;padding:1.25rem 1.25rem .5rem;position:relative}.icon{font-size:2.5rem;margin-right:.5rem}.customBadge{background:rgba(76,201,240,.2);color:#4cc9f0;padding:.25rem .75rem;border-radius:50px;font-size:.7rem;font-weight:600;text-transform:uppercase;letter-spacing:.5px}.completedBadge{background:rgba(63,55,201,.2);color:#3f37c9;padding:.25rem .75rem;border-radius:50px;font-size:.7rem;font-weight:600;text-transform:uppercase;letter-spacing:.5px}.content{padding:0 1.25rem 1.25rem;flex-grow:1;display:flex;flex-direction:column}.title{font-size:1.25rem;font-weight:700;margin:.5rem 0 .75rem;color:#212529;line-height:1.3}.description{font-size:.9rem;color:#6c757d;margin-bottom:1rem;line-height:1.5;flex-grow:1}.meta{display:flex;justify-content:space-between;margin-bottom:1.25rem;font-size:.8rem}.category{background:rgba(67,97,238,.1);color:#4361ee;padding:.3rem .8rem;border-radius:50px;font-weight:600}.daysActive{color:#adb5bd;display:flex;align-items:center}.daysActive::before{content:"⏱️";margin-right:.3rem}.progressContainer{margin:1rem 0}.progressInfo{display:flex;justify-content:space-between;font-size:.8rem;color:#6c757d;margin-bottom:.5rem}.progressBar{height:8px;background:#e9ecef;border-radius:4px;overflow:hidden;margin-bottom:.5rem}.progressFill{height:100%;background:linear-gradient(90deg, #4361ee, #4cc9f0);border-radius:4px;transition:width .6s ease}.progressFill.completed{background:#3f37c9}.progressPercentage{text-align:right;font-size:.8rem;font-weight:600;color:#6c757d}.actions{margin-top:auto}.editMode{display:flex;flex-direction:column;gap:.75rem}.progressInput{padding:.75rem;border:1px solid #e9ecef;border-radius:8px;font-size:1rem;width:100%}.progressInput:focus{outline:none;border-color:#4361ee;box-shadow:0 0 0 2px rgba(67,97,238,.2)}.editButtons{display:flex;gap:.75rem}.actionButtons{display:flex;flex-wrap:wrap;gap:.75rem}.buttonPrimary{background:#4361ee;color:#fff;border:none;padding:.7rem 1rem;border-radius:8px;font-size:.9rem;font-weight:600;cursor:pointer;transition:all .2s ease;flex-grow:1}.buttonPrimary:hover{background:#3a56d4;transform:translateY(-2px)}.buttonPrimary:active{transform:translateY(0)}.buttonSecondary{background:#fff;color:#4361ee;border:1px solid #4361ee;padding:.7rem 1rem;border-radius:8px;font-size:.9rem;font-weight:600;cursor:pointer;transition:all .2s ease;flex-grow:1}.buttonSecondary:hover{background:rgba(67,97,238,.1);transform:translateY(-2px)}.buttonSecondary:active{transform:translateY(0)}.completeButton{background:#3f37c9;color:#fff;border:none;padding:.7rem 1rem;border-radius:8px;font-size:.9rem;font-weight:600;cursor:pointer;transition:all .2s ease;width:100%}.completeButton:hover{background:#2e28a1;transform:translateY(-2px)}.completeButton:active{transform:translateY(0)}.addButton{background:#4361ee;color:#fff;border:none;padding:.8rem;border-radius:8px;font-size:.95rem;font-weight:600;cursor:pointer;transition:all .2s ease;width:100%;margin-top:1rem}.addButton:hover{background:#3a56d4;transform:translateY(-2px);box-shadow:0 1px 3px rgba(0,0,0,.12)}.addButton:active{transform:translateY(0)}.completedActions{margin-top:auto}.completionCelebration{text-align:center;font-weight:600;color:#3f37c9;margin:1rem 0;animation:bounce .6s}@keyframes pulse{0%{box-shadow:0 0 0 0 rgba(67,97,238,.4)}70%{box-shadow:0 0 0 10px rgba(67,97,238,0)}100%{box-shadow:0 0 0 0 rgba(67,97,238,0)}}@keyframes bounce{0%,20%,50%,80%,100%{transform:translateY(0)}40%{transform:translateY(-10px)}60%{transform:translateY(-5px)}}